                  width="100"
                  src="@/assets/images/logo.jpg"
                  alt=""
                ></v-img>
              </router-link>
            </div>
          </v-toolbar-title>
          <v-col cols="12" md="7">
            <div class="search-bar d-flex p-relative">
              <v-text-field
                v-model="textSearchVar"
                on-change
                class=""
                :placeholder="textSearch"
                filled
                rounded
                hide-details
                dense
                prepend-inner-icon="mdi-magnify"
              ></v-text-field>
              <v-btn
                color="primary"
                class="text-capitalize search-bar-dropdown px-10 font-600"
              >
                Buscar
              </v-btn>
              <!-- {{textSearch}} -->
            </div>
          </v-col>
          <div class="d-md-block d-none">
            <!-- cartCount  -->
            <v-badge
              bordered
              color="error"
              :content="
                getCartProducts.length == 0 ? '0' : getCartProducts.length
              "
              overlap
            >
              <v-btn
                @click="drawer = true"
                elevation="0"
                fab
                color="grey lighten-2"
                small
              >
                <v-icon color="">mdi-cart</v-icon>
              </v-btn>
            </v-badge>
          </div>
        </div>
