<script>
import { mapGetters, mapActions } from "vuex";
// import Navbar from "@/views/home/navbar";
import { directive as onClickaway } from "vue-clickaway";
import drawerNav from "../../components/DrawerNav.vue";

export default {
  name: "BaseAppBar",
  directives: {
    onClickaway: onClickaway,
  },
  components: {
    // Navbar,
    drawerNav,
  },

  computed: {
    ...mapGetters(["getCartProducts"]),
    cartTotal() {
      let total = 0;
      this.getCartProducts.forEach((product) => {
        total += product.precio * product.cantidadInit;
      });
      return total;
    },
    // textSearch(){
    //   this.addTextSearch(textSearchVar);

    //   return textSearchVar
    // },
    textSearch: {
      // getter
      get() {
        this.addTextSearch(this.textSearchVar);
        return this.textSearchVar;
      },
      // // setter
      // set(newValue) {
      //   // Note: we are using destructuring assignment syntax here.
      //   this.addTextSearch(newValue);
      // }
    },
  },
  data: () => ({
    textSearchVar: "",
    cartCount: 0,
    colorNav: false,
    dialog: false,
    isToggleNavbar: false,
    itemTwo: ["Foo", "Bar", "Fizz", "Buzz"],
    drawer: false,
    group: null,
  }),

  methods: {
    ...mapActions(["addCart", "removeCart", "addTextSearch"]),
    orden() {
      console.log("MI ORDEN ES");
    },
    toggleNavbar() {
      if (this.$route.path == "/") {
        this.isToggleNavbar = false;
      } else {
        this.isToggleNavbar = !this.isToggleNavbar;
      }
    },
    away() {
      this.isToggleNavbar = false;
    },
    removeCartx(item) {
      this.removeCart(item);
    },
    addCartx(item) {
      this.addCart(item);
    },
  },
};
</script>
